// Toggle Switch Variables:
// --sb-toggleswitch-bg-color - Background color of switch area
// --sb-toggleswitch-border-color - Border color of switch area
// --sb-toggleswitch-border-radius - Border radius to apply to the switch container and switch highlight
// --sb-toggleswitch-border-width - Border width to apply to the switch container
// --sb-toggleswitch-transition-speed - Speed of the slide effect
// --sb-toggleswitch-color - Color of the text/icons
// --sb-toggleswitch-highlight-color - Background color of selected switch
// --sb-toggleswitch-selected-color - Color of the selected switch

.sb-toggle-switch {
  background-color: var(
    --sb-toggleswitch-bg-color,
    var(--sbt-color-background)
  );
  border: var(--sb-toggleswitch-border-width, 1px) solid
    var(--sb-toggleswitch-border-color, var(--sbt-theme-tertiary-color-20));
  border-radius: var(--sb-toggleswitch-border-radius, 20px);
  display: inline-flex;
  flex-direction: row;
  overflow: hidden;
  position: relative;
}

.sb-toggle-switch-elements {
  z-index: var(--sbt-stack-floating);
}

.sb-toggle-switch-element {
  --sb-button-color: transparent;
  --sb-button-color-light: transparent;
  --sb-button-color-contrast: var(
    --sb-toggleswitch-color,
    var(--sbt-color-text)
  );
  --sb-button-color-dark: transparent;
  --sb-button-border-radius: 0;
  --sbt-elevation-40: 0;
  box-sizing: content-box;
  margin: 0;
  padding: 10px;
}

.sb-toggle-switch-selected {
  --sb-button-color-contrast: var(
    --sb-toggleswitch-selected-color,
    var(--sbt-color-text-invert)
  );
}

.sb-toggle-switch-highlight {
  background-color: var(
    --sb-toggleswitch-highlight-color,
    var(--sbt-theme-color-70)
  );
  border-radius: var(--sb-toggleswitch-border-radius, 20px);
  bottom: 0;
  height: 100%;
  padding: 0;
  position: absolute;
  top: 0;
  transition: all var(--sb-toggleswitch-transition-speed, 300ms) ease-in-out;
  width: 0; /* Gets overriden inline */
  z-index: var(--sbt-stack-base);
}
